.activity_detail {
    position: relative;
}

.activity_detail.left-bar::before {
    content: '';
    position: absolute;
    top: 2rem;
    height: 90%;
    width: 1px;
    background: rgba(0, 0, 0, .3);
    left: -.8rem;
}

.activity_detail::after {
    content: '';
    position: absolute;
    top: 0;
    width: 4.5px;
    height: 4px;
    left: -.95rem;
    top: 1rem;
    border-radius: 100%;
    box-shadow: 0 0 0 3px #7874ff;
    font-size: 1.2rem;
}